export default function ProductListing() {
  const products = [];
  for (let i = 0; i < Math.min(testImages.length, testNames.length); i++) {
    products.push({
      brand: 'Chanel',
      name: testNames[i],
      image: testImages[i],
      retailPrice: 300000 + 1000 * i,
      salesPrice: 250000 + 1000 * i,
    });
  }
  return (
    <ScrollView>
      <View style={styles.container}>
        {products.map((product, i) => {
          return <ProductCard key={`product-card-${i}`} {...product} id={i+1} />
        })}
      </View>
    </ScrollView>
  );
}

